:root{
  --gap : 35px;
}

#root > *{
  margin-bottom: var(--gap);
}

#root{
  padding: var(--gap);
}
.board{
  display: inline-flex;
  flex-direction: column;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}
.row{
  display: inline-flex;
}

.box{
  font-size: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50px;
  border-left: black solid 1px;
  border-top: black solid 1px;
}

.buttons{

  vertical-align: top;
  align-items: flex-start;
  display: inline-flex;
  flex-direction: column;
  margin-left: var(--gap);
}